import React from 'react';
// import {PathLine} from 'react-svg-pathline'
import './App.css';
import * as utils from './utils.js';
import axios from 'axios';
import { Container, Row, Col, Form} from 'react-bootstrap';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      width: 100,
      height: 100,
      history: [{
        boardState: utils.create_board(173538815, 562399469895680),
      }],
      selected: null,
      step_number: 0,
      has_moved: null,
      available_move: [],
      visited:[],
      must_choose: [],
      computer_move: false,
      move_log:[],
      move_logback:[],
      states:[],
      is_moving: false,
      was_capture: false,
      status: null,
      opponent:'computer',
      move_first:'human',
      depth:3,
      move_string: [],
      history_turn: [{
        boardState: [],
        turn_id: 0,
        human:'',
        computer:'',
      }],
      turn_number: 0,
    };
  }


  componentDidUpdate = () => {
    if (this.state.computer_move) {
      this.setState({is_moving: true});
      this.wait_computer();
      this.setState({computer_move: false, is_moving: false});
      console.log(this);
    }
  }


  computer_turn(boardstate, was_capture, depth) {
    let current_params = {'boardstate':boardstate, 'was_capture':was_capture, 'depth':depth};
    //let history = this.state.history;
    //let current_step = this.state.step_number;
    // history = history.slice(0, current_step + 1);
    let selected_list=[];
    let states = [];
    axios.post('/pass', current_params)
    .then(res => {
        let move_log = res.data["move_log"];
        let movedict = res.data['movedict'];
        let selected;
        let prev_selected = move_log[0];
        for (var i=0; i<move_log.length; i++){
          boardstate[prev_selected] = 'none';
          selected = move_log[i];
          boardstate[selected] = 'two';
          boardstate = boardstate.map(function(item) { return item === 'none eaten' ? 'none' : item; });
          var eaten = movedict[selected];
          for (var j=0; j<eaten.length; j++){
            boardstate[eaten[j]] = 'none eaten';
          }
          prev_selected = selected;
          selected_list = selected_list.concat([prev_selected]);
          states = states.concat([boardstate]);
        }
        this.setState({move_log: selected_list, move_logback:selected_list, states:states});
        setTimeout(()=> {
            this.iterate_move(selected_list, states);}, 1000);
    })
    .catch(err => {
        alert(err);});
    }
  

  iterate_move(move_log, states){
    setTimeout(()=> {
      let selected = move_log.shift();
      let board = states.shift();
      let visited = this.state.visited;
      if (move_log.length === 0){
        visited.push(selected);
        this.resetState(board);
        this.setState({computer_move: false, visited: []});
        return
      }
      visited.push(selected);
      this.setState({history: this.state.history.concat([{
        boardState: board,}]), selected: selected, step_number: this.state.history.length-1, visited: visited});
      this.iterate_move(move_log, states)
    }, 1000);
  }

  wait_computer(){
    const board = this.getCurrentState();
    var was_capture = this.state.was_capture;
    var depth = this.state.depth;
    this.computer_turn(board, was_capture, depth);
  }


  AImoves(){
      const move_log = this.state.move_log;
      const states = this.state.states;
      this.iterate_move(move_log, states);
  }


  getCurrentState() {
    var history = this.state.history;
    var boardlist;
    if (history.length > 1){
      history = history.slice(0, this.state.step_number + 1);
      boardlist = history[history.length - 1];
    } else {
      boardlist = history[0];
    }
    return boardlist.boardState;
  }

  AppendHistory(history) {
    var history_turn = this.state.history_turn;
    return history_turn.concat([history])
  }



  restart_board = () => {
    this.setState({
      history: [{
        boardState: utils.create_board(173538815, 562399469895680),
      }],
      selected: null,
      step_number: 0,
      has_moved: null,
      available_move: [],
      must_choose:[],
      status: 'Human to move',
    });
  }
  

  undo_move = () => {
    const backStep = parseInt(this.state.step_number, 10) -1;
    if (backStep < 0) {
        return;
    }
    const unsetHistory = this.state.history.slice(0, backStep+1);
    this.setState({
        history: unsetHistory,
        selected: null,
        available_move: [],
        visited: [],
        must_choose: [],
        step_number: backStep,
        new_pos: null,
    });
  }


  resetState = (newboard) => {
    var visited = this.state.visited;
    let move_string = utils.tomoveString(visited);
    let turn = this.state.turn_number;
    if (this.state.is_moving){
      var history_turn = this.state.history_turn;
      history_turn[turn].computer = move_string;
    } else{
      turn = turn + 1;
      var history = {boardState: newboard, turn_id: turn, human:move_string, computer:''};
      var history_turn = this.AppendHistory(history);
    }
    
    this.setState({history: this.state.history.concat([{
      boardState: newboard, }]),
    selected: null, 
    computer_move: false,
    available_move: [],
    visited: [],
    has_moved: false,
    must_choose: [],
    step_number: this.state.history.length,
    status: 'Computer is moving',
    history_turn: history_turn ,
    turn_number: turn,
    });
  }


  pass_game = () => {
    if (!(this.state.has_moved)){
      return;
    }
    var visited = this.state.visited;
    let move_string = utils.tomoveString(visited);
    var turn = this.state.turn_number + 1;
    var newboard = this.getCurrentState();
    var history = {boardState: newboard, turn_id: turn, human:move_string, computer:''};

    this.setState({was_capture: true, computer_move: true, 
      selected: null, status: 'Computer is moving',
      available_move: [],
      visited: [],
      has_moved: false,
      must_choose: [],
      history_turn: this.AppendHistory(history),
      turn_number: turn,
    });
  }

  handle_opp_change = (event) =>{
    this.setState({opponent: event.target.value});
  }

  handle_movefirst = (event) =>{
    this.setState({move_first: event.target.value});

  }
  handle_depth = (event) =>{
    this.setState({depth: event.target.value});
  }


  onClick = (i) => {
    if (this.state.computer_move){
      return
    }
    // previous selection must choose direction
    var must_choose = this.state.must_choose;
    const selected = this.state.selected;
    const boardstate = this.getCurrentState();
    var visited = this.state.visited;
    var movetype, possible_move, newboard, must_capture;
    var capture_backward, capture_forward;
    var has_moved = this.state.has_moved;
    /* if no piece was selected:
      - return if the next selected is not my_pieces
      - else: check if the selected piece can move and return available move
    */



    if (must_choose.length > 1){
      if (!(must_choose.includes(i))){
        alert("you must choose");
        return
      } else {
        const new_pos = this.state.new_pos;
        newboard = utils.makeMove_choosen(boardstate, selected, i)
        newboard[selected] = 'none';
        newboard[new_pos] = 'one';
        if (!(has_moved)){
          visited.push(selected);
        }
        visited.push(new_pos);
        must_capture = utils.MustCapture(newboard, true);
        possible_move = utils.legalMove(boardstate, i, visited, must_capture);
        if ((must_capture) && (possible_move  > 0)){
          this.setState({history: this.state.history.concat([{
            boardState: newboard, currentPlayer: true,}]),
            must_choose: [], 
            selected: new_pos,
            visited: visited,
            has_moved: true});
        } else{
          this.resetState(newboard);
          this.setState({was_capture: true, computer_move: true, visited: []});
          return;
        }
      }
    }

    must_capture = utils.MustCapture(boardstate, this.state.has_moved);

    const available_move = this.state.available_move;
    
    // if clicked the selected piece twice undo selection
    if ((selected === i) && (!(has_moved))) {
      this.setState({selected: null, available_move: []});
      return;
    }
    if ((selected) && (boardstate[i] === 'one') && (!(has_moved))){
      possible_move = utils.legalMove(boardstate, i, visited, must_capture);
      if (possible_move.length > 0){
        this.setState({selected: i, available_move: possible_move});
      } else {
       return
      }
    }

    
    /* No pieces selected */
    if (!(selected) && (! (this.state.computer_move))){
      if (boardstate[i] !== 'one'){
        return
      } else {
        possible_move = utils.legalMove(boardstate, i, visited, must_capture);
        // if it can move mark it as selected and show available move
        if (possible_move.length > 0){
           this.setState({selected: i, available_move: possible_move});
        } else{
          return
        }
      }
    }


    if (available_move.includes(i)){
      movetype = i - selected;
      // Check if both forward and backward direction are possible
      capture_forward = selected + 2*movetype;
      capture_backward = selected - movetype;

      if ((boardstate[capture_forward] ==='two') && (boardstate[capture_backward] ==='two')){
        this.setState({must_choose: [capture_forward, capture_backward], new_pos:i});
        alert('must choose');
        return
      }

      newboard = utils.makeMove(boardstate, selected, i, movetype);
      if (!(has_moved)){
        visited.push(selected);
      }
      visited.push(i);
      
      if (!must_capture){
        this.resetState(newboard);
        this.setState({was_capture: false, computer_move: true, visited: []});
        return;
        // this.resetState(result.boardstate);
      }

      
      possible_move = utils.legalMove(newboard, i, visited, must_capture);
      if (possible_move.length>=1){
        this.setState({history: this.state.history.concat([{
          boardState: newboard, 
          currentPlayer: true,}]),
        selected: i, 
        available_move: possible_move,
        has_moved: true,
        visited: visited,
        must_choose: [],
        step_number: this.state.history.length,});
      } else{
          this.resetState(newboard);
          this.setState({was_capture: true, computer_move: true, visited: []});
          return;
      }
    }
  }
  
  getTurn(history_turn){
    let new_history=[];
    if (history_turn.length === 1){
      new_history.push([{turn:1, human:history_turn[0], computer:''}]);
    } else {
      for (var i = 0; i < history_turn.length; i+2){
        new_history.push([{turn:(i/2)+1, human:history_turn[i], computer:history_turn[i+1]}]);
        console.log(i);
      }
    }
    return new_history;
  }
  


  render() {
    const boardstate = this.getCurrentState();
    const selected = this.state.selected;
    const available_move = this.state.available_move;
    const winner = utils.is_winner(boardstate);
    const must_choose = this.state.must_choose;
    // const status = this.status;
    if (winner){
      alert("game over: " + winner);
      this.restart_board();
    }
    let status;
    if (this.state.is_moving){
      status = 'Computer is moving...';
    } else if (must_choose.length){
      status = 'Choose: '+utils.ToSquare(must_choose[0])+' or '+utils.ToSquare(must_choose[1]);
    } else if (selected){
      status = 'Piece selected: '+utils.ToSquare(selected);
    } else {
      status = 'Human to move';
    }
    let hist_in = this.state.history_turn.slice(1);
    console.log(hist_in);
    const new_history = this.getTurn(hist_in);
    console.log(new_history);
    return (
      <div className='main'>
        <Container fluid={true} id={'container1'}>
          <Row noGutters={true}>
            <Col xs={12} sm={12} md={8}>
              <Form className='form_command'>
                <Form.Row className="justify-content-md-center"> 
                  <Form.Group as={Col} md="3" xs="4" controlId="opponent">
                    <Form.Label>Opponent</Form.Label>
                    <Form.Control value={this.state.opponent} as="select" onChange={this.handle_opp_change}>
                        <option value="computer">Computer</option>
                        <option value="human">Human</option>
                    </Form.Control>
                  </Form.Group>

                  <Form.Group as={Col} md="3"  xs="4" controlId="move_first">
                    <Form.Label>Move First</Form.Label>
                    <Form.Control value={this.state.movefirst} as="select" onChange={this.handle_movefirst} >
                      <option value="computer">Computer</option>
                      <option value="human">Human</option>
                    </Form.Control>
                  </Form.Group>

                  <Form.Group as={Col} md="3"  xs="4" controlId="depth">
                    <Form.Label>Depth</Form.Label>
                    <Form.Control value={this.state.depth} as="select"  onChange={this.handle_depth}>
                      <option>1</option>
                      <option>2</option>
                      <option>3</option>
                      <option>4</option>
                      <option>5</option>
                    </Form.Control>
                  </Form.Group>
                </Form.Row>
              </Form>
            </Col>
          </Row>
          <Row noGutters={true} className='windowa'>
          <Col xs={12} sm={12} md={8}  className='game_windows row-eq-height'>
            <div className='aspect_ratiodiv'>
              <Container fluid={true} className='BoardCont' >
                  <Row noGutters={true} className="crosscont"  >
                    <div className='aspect_ratiodiv1'>
                    <Board
                      boardstate={boardstate}
                      selected = {selected}
                      available_move = {available_move}
                      onClick={i => this.onClick(i)}
                    />
                    </div>
                  </Row>
                  <Row noGutters={true} className="command_button" >
                      <Col style={{ textAlign: "center" }}>
                        <button  className="game_button"  onClick={() => this.restart_board()}>Restart</button>
                      </Col>
                      <Col style={{ textAlign: "center" }}>
                        <button  className="game_button"   onClick={() => this.pass_game()}>Pass</button>
                      </Col>
                      <Col style={{ textAlign: "center" }}>
                        <button  className="game_button"   onClick={() => this.undo_move()}>Undo</button>   
                      </Col>
                  </Row>
                  
              </Container>
            </div>
          </Col>
          <Col xs={12} sm={12} md={4} className="status_window row-eq-height">
            <MoveStatus 
              status={status} move_string={new_history}
              //move_table={move_table}
            />

          </Col>
          </Row>
        </Container>
      </div>
    )
  }
};


const Stones = (props) => {
  const id = props.id;
  const idbox = utils.ToSquare(id);
  const selected = props.selected;
  const available = props.available;
  var classname = "player-"+props.stoneClasses +' '+idbox;
  if (id === selected){
    classname = classname + '  selected';
  }
  if (available.indexOf(id) > -1) {
    classname = classname + '  available';
  }  
  return (
      <div className={classname}  id={id} onClick={props.onClick}>
      </div>
  );
}



class MoveStatus extends React.Component {
  renderTableData() {
    const move_str = this.props.move_string;
    console.log(move_str);
    return move_str.map((history, index) => {
        const {turn_id, human, computer } = history //destructuring
        return (
          <tr key={turn_id}>
             <td>{turn_id}</td>
             <td>{human}</td>
             <td>{computer}</td>
          </tr>
        )
    })
 }
  render(){
    const status = this.props.status;
    return (
      <div className="box_status">
      <Container fluid={true} className="status_show">
        {/* <Row noGutters={true} style={{fontWeight: 'bold'}}>Game Status</Row> */}
        <h3>Game Status</h3>
        <Row noGutters={true} className="game_status">
          <Col className="col-centered" id="log_status" >
            <div className="center-me" id="statusbox"> {status}
            </div>
          </Col>
        </Row>
        {/* <Row noGutters={true} style={{fontWeight: 'bold'}}> Moves</Row>  */}
        <h3>Moves</h3>
        <Row noGutters={true} className="moves_log">
          <Col className="moves_tables">
            <div id="statusall">
              <table id='moves'>
                <tbody>
                    {this.renderTableData()}
                </tbody>
              </table>
            </div>
          </Col>
        </Row>              
      </Container>
    </div>
    )
}
}



